@font-face {
    font-family: 'Roboto Thin';
    src: local('Roboto Thin'), url(./fonts/Roboto/Roboto-Thin.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto Regular';
    src: local('Roboto Regular'), url(./fonts/Roboto/Roboto-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'Caveat';
    src: local('Caveat'), url(./fonts/Caveat/Caveat-VariableFont_wght.ttf) format('truetype');
}

body {
	background-color: rgb(22, 105, 120);
	background-size: 400% 400%;
}

/* COMPONENTS */

.subtitle_black{
    font-family: 'Roboto Thin', sans-serif;
    font-size: 32px;
    margin-left: 150px;
    color: black;
}

.subtitle_white{
    font-family: 'Roboto Thin', sans-serif;
    font-size: 32px;
    margin-left: 150px;
    color: white;
}

.title_black{
    font-family: 'Roboto Regular', sans-serif;
    font-size: 38px;
    font-weight: bold;
    margin-left: 50px;
    color: black;
}

.title_white{
    font-family: 'Roboto Regular', sans-serif;
    font-size: 38px;
    font-weight: bold;
    margin-left: 50px;
    color: white;
}

.arrows{
    width: 50px;
    margin: 100px 30px;
}

.arrow_sizing{
    width: 50px;
    height: 50px;
}

.img_sizing{
    height: 75px;
    width: 75px;
    margin: 2px;
}

/* PROFILE CSS */

.profile{
    padding: 20px;
}

.profile_name {
    text-align: left;
    margin-left: 50px;
    margin-bottom: 10px;
    font-family: 'Caveat', sans-serif;
    font-size: 92px;
}

.profile_infos{
    text-align: left;
    margin: 75px 150px;
    font-family: 'Roboto Thin', sans-serif;
    font-size: 28px;
    color:white;
}

.profile_menu{
    margin-bottom: 35px;
}

.profile_menu > *{
    display: flex;
    margin: 20px 100px 20px 1050px;
    font-family: 'Roboto Thin', sans-serif;
    font-size: 30px;
    color: grey;
    border: 2px solid grey;
    background-color: white;
    opacity: 50%;
    border-radius: 10px;
    padding: 0 30px;
    line-height: normal;
}

/* CERTIFICATION CSS */

#certification{
    padding: 20px;
    background: linear-gradient(rgb(22, 105, 120), rgb(255, 255, 255), rgb(255, 255, 255));
}

.bar{
    width: 1200px;
    margin-top: 40px;
    height: 3px;
    background-color: black;
    border-radius: 10px;
}

    /* DIPLOMAS */

    .diploma_display{
        margin-left: 50px;
        display: flex;
    }

    .diploma{
        border: 1px solid black;
        padding: 30px;
        margin: 10px 10px 10px 75px;
        width: 250px;
        text-align: center;
        border-radius: 10px;
        background-color: rgb(101, 142, 150);
    }

    .diploma:hover{
        background-color: rgb(22, 105, 120);
    }
    
    .diploma_title{
        color: white;
        font-size: 26px;
        font-weight: bold;
    }

    /* LANGUAGES */

    .language_display{
        margin-left: 100px;
        display: flex;
    }
    
    .language_display p{
        margin-left: 30px;
        font-weight: bold;
    }

    .language_title{
        font-family: 'Roboto Thin', sans-serif;
        font-size: 24px;
        margin-left: 30px;
    }

    .PBContainer{
        width: 300px;
        height: 20px;
        background-color: #e0e0de;
        border-radius: 50px;
        margin-left: 50px;
        margin: 30px;
        border: 1px solid black;
    }
    
    .PBFillerAnglais{
        margin-left: 0 !important;
        height: 100%;
        width: 91.5%;
        background-color: rgb(22, 105, 120);
        border-radius: inherit;
        padding-right: 0;
        text-align: left;
    }
    
    .PBFillerEspagnol{
        margin-left: 0 !important;
        height: 100%;
        width: 70%;
        background-color: rgb(22, 105, 120);
        border-radius: inherit;
        padding-right: 0;
        text-align: left;
    }

/* Experience */

#experience{
    background-color: rgb(80, 138, 80);
    padding: 20px;
}

.experience_display{
    display: flex;
    margin-left: 450px;
}

.exp{
    opacity: 50%;
    color: white;
}

.expActive{
    opacity: 100%;
    margin: 35px 0;
    color: white;
}

.expHidden{
    opacity: 0;
}

/* TOOLS */

#tools{
    padding: 20px;
    background-color: rgb(96, 141, 96);
}

.tool_display{
    margin: 100px 250px;
    padding-bottom: 100px;
    border: 1px solid grey;
    border-radius: 20px;
    background-color: rgb(152, 206, 152);
}

.tool_title{
    text-align: center;
    color: white;
    font-family: 'Roboto Thin', sans-serif;
    font-size: 30px;
}

.tool_container{
    display: flex;
    margin-left: 180px;
    margin-top: 70px;
}

.tool_container_display{
    display: flex;
    border: 1px solid black;
    padding: 5px 20px;
    background-color: white;
    border-radius: 10px;
}
    
.tool_container_display img{
    padding: 12px;
}

.langHidden{
    display: none;
}

.PBContainerLittle{
    width: 100px;
    height: 10px;
    background-color: #e0e0de;
    border-radius: 50px;
    margin: 20px;
    border: 1px solid black;
}

.PBFillerLittle{
    margin-left: 0 !important;
    height: 100%;
    background-color: rgb(32, 97, 30);
    border-radius: inherit;
    padding-right: 0;
    text-align: left;
}

/* HOBBYS */

#hobbys{
    background-color: rgb(96, 141, 96);
}

.hobbys_display{
    display: flex;
    margin-left: 100px;
    padding-bottom: 30px;
    margin-top: 50px;
}

.hobbys_display div{
    width: 50%;
}

.hobbys_title{
    font-weight: bold;
    font-size: 20px;
}

.hobbys_display p{
    text-align: 'center';
    color: white;
    width: 75%;
    font-style: 'Roboto Thin', 'sans-serif';
}


/* CONTACT */

#contact{
    padding: 20px;
    background-color: black;
}

.contact_infos{
    color: white;
    font-family: 'Roboto Thin', sans-serif;
    margin-left: 80px;
    font-size: 18px;
    display: flex;
}

.github{
    margin-left: 500px;
}